import React,{useState,useEffect} from 'react';
import {NativeSelect,FormControl} from '@material-ui/core';
import {makeStyles} from '@material-ui/core/styles';

import styles from './CountryPicker.module.css';
import { countries } from '../../api';

const useStyles=makeStyles({
    base:{
        backgroundColor:'#FFF7F8',
        marginTop: '10px',
        marginBottom:'25px',
        paddingLeft:'5px'
    }
})

const CountryPicker = ({countryChange}) => {
    const [country,setCountry]=useState();

    useEffect(()=>{
        const fetchCountries=async()=>{
            setCountry(await countries());
        }
        fetchCountries();
    },[setCountry]);

    const classes=useStyles();

    return(
       <FormControl className={styles.FormControl}>
           <NativeSelect className={classes.base} defaultValue="" onChange={(e)=>countryChange(e.target.value)}>
               <option value="us">US</option>
               {country?country.map((c,index)=><option key={index} value={c}>{c}</option>)
               :""}
           </NativeSelect>
       </FormControl>
    );
}

export default CountryPicker;